import React, { useState } from 'react';
import '../login/login.scss'; // Import file CSS cho biu mu (hy to file CSS tng ng)
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault(); // Ngn chn hnh vi mc nh ca form submission
    const userData = {
      email: email,
      password: password,
    };

    try {
      const response = await axios.post("https://localhost:7249/api/LoginAdmin/login", userData);
      console.log(response);

      const accessToken = response.data.token;

      // Lu accessToken vo localStorage
      localStorage.setItem('accessToken', accessToken);

      // Chuyn hng n trang chnh
      navigate('/home');
    } catch (error) {
      console.error('Error:', error);
      // X l li ng nhp
      // V d: hin th thng bo li cho ngi dng
    }
  };

  return (
    <div className="login-container">
        <div className="login-form">
          <h2>Login</h2>
          <form>
            <div className="form-group">
              <label>Email</label>
            <input
              type="text"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Enter your email "  
            />
            
          </div>
          <div className="form-group">
            <label>Password</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}  
              placeholder="Enter your password"
            />
          </div>
          <button onClick={handleLogin}>Login</button> 
        </form>
      </div>
    </div>
  );
}

export default Login;
