import "./viewdetail.scss";
import HomeTwoToneIcon from '@mui/icons-material/HomeTwoTone';
import DescriptionTwoToneIcon from '@mui/icons-material/DescriptionTwoTone';
import LoyaltyTwoToneIcon from '@mui/icons-material/LoyaltyTwoTone';
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';
import TodayIcon from '@mui/icons-material/Today';
import CategoryIcon from '@mui/icons-material/Category';
import { Link } from "react-router-dom";
import Button from '@mui/material/Button';
import React, { useState, useEffect  } from 'react';
import axios from 'axios';


const Viewdetail = () => {
  const [data, setData] = useState([]);
  
  //const [amenityData, setAmenityData] = useState([]);
  // Fetch data on component mount
  useEffect(() => {
    getData();
    //getAmenityData();
  }, []);

  const getData =() => {
    axios
      .get(`https://localhost:7249/api/Stay/GetListStay`)
      .then((result) => {
        setData(result.data);
        console.log(result.data);
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
      });
  };
  /*const getAmenityData = useCallback(() => {
    axios
      .get(`https://localhost:7249/api/Amenity/AmenityList/${id}`)
      .then((result) => {
        setAmenityData(result.data.data);
        console.log(result.data);
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
      });
  }, []); */

  return (
    <div className="viewdetail-container">
      <div className="viewdetail-form">
        <form>
          {data.length > 0 ? (
            data.map((item, index) => (
              <div key={index} className="data-item">
                <img src={`https://localhost:7249/Images/${item.featuredImage}`} alt={item.dateCreated} />
                <h3>Title: {item.title}</h3>

                <HomeTwoToneIcon className="icon"/>
                <span>Address: {item.address}</span>
                
                <hr/>
                <DescriptionTwoToneIcon className="icon"/>
                <span>Description: {item.description}</span>

                <hr/>
                <LoyaltyTwoToneIcon className="icon"/>  
                <span>SaleOff: {item.saleOff}</span>
                
                <hr/>
                <MonetizationOnIcon className="icon"/>
                <span>Price: {item.price?.basePrice || 'N/A'} {item.price?.currency || ''}</span>
                
                <hr/>
                <CategoryIcon className="icon"/>
                <span>Category: {item.category?.propertyType || 'N/A'} / {item.category?.rentalform}</span>
              
                <hr/>
                <TodayIcon className="icon"/>
                <span>Date Created: {item.dateCreated}</span>
                <hr/>
              </div>
            ))
          ) : (
            <span>No Data</span>
          )}
        </form> 
       
      </div>
      <Link to="/stay" style={{ textDecoration: "none" }}>
        <Button variant="outlined">Back Table</Button>
      </Link>
    </div>
    
  );
};

export default Viewdetail;
