import { createContext, useReducer } from 'react';

const INITIAL_STATE = {
  user: null,          // Trng thi ban u ca ngi dng (cha ng nhp)
  loading: false,      // Trng thi ban u cho vic ti d liu
  error: null,         // Trng thi ban u cho thng bo li
};

// To AuthContext
export const AuthContext = createContext(INITIAL_STATE);
/* AuthContext l mt context React c to bng createContext. N s lu tr trng thi ng nhp ca ngi dng v cung cp cho cc thnh phn con.
AuthReducer l mt hm reducer, c s dng  thay i trng thi da trn cc hnh ng (action) nh "LOGIN_START", "LOGIN_SUCCESS", "LOGIN_FAILURE", "LOGOUT". y l ni bn quyt nh cch cp nht trng thi ca ng dng.
AuthContextProvider l mt thnh phn gc ca ng dng m bn s bc bn ngoi ng dng ca bn. N cung cp trng thi v hm dispatch thng qua context cho ton b ng dng.*/
const AuthReducer = (state, action) => {
  switch (action.type) {
    case "LOGIN_START":
      return {
        user: null,    // t ngi dng thnh null khi ng nhp bt u
        loading: true, // t trng thi loading l true
        error: null,   // t li thnh null
      };
    case "LOGIN_SUCCESS":
      return {
        user: action.payload, // t ngi dng thnh d liu  ng nhp
        loading: false,       // Kt thc vic ti
        error: null,          // t li thnh null
      };
    case "LOGIN_FAILURE":
      return {
        user: null,           // t ngi dng thnh null v ng nhp tht bi
        loading: false,       // Kt thc vic ti
        error: action.payload, // Lu thng bo li
      };
    case "LOGOUT":
      return {
        user: null,           // t ngi dng thnh null khi ng xut
        loading: false,       // Kt thc vic ti
        error: action.payload, // Lu thng bo li (nu c)
      };
    default:
      return state;
  }
};

export const AuthContextProvider = ({ children }) => {
  const [state, dispatch] = useReducer(AuthReducer, INITIAL_STATE);

  // useEffect(() => {
  //   localStorage.setItem("user", JSON.stringify(state.user));
  // }, [state.user]);

  return (
    <AuthContext.Provider
      value={{
        user: state.user,
        loading: state.loading,
        error: state.error,
        dispatch,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
